import { useState, useEffect } from "react";
import React from "react";
import { ChartApp } from "../Chart";
import { getCalculatorData } from "./data";
import { getCalculators } from "./data";



const getOutput = (nameOfFunction, argumentsObject) => {
  const result = nameOfFunction(argumentsObject);
  const decoratedResult =result.toLocaleString('en-IN', {
    maximumFractionDigits: 2,
    currency: 'INR'
})
  return decoratedResult;
};

export function Calc(props) {
  const [inputData, setInputData] = useState({});

  const [selectedIndex, setSelectedIndex] = useState(0);
  const [isMultiple, setIsMultiple] = useState(false);
  const [calculators, setCalculators] = useState([]);
  const [selectedProduct, setSelectedProduct] = useState("");

  useEffect(() => {
    setSelectedProduct(props.product.title);
  });

  useEffect(() => {
    let newInputData = null;
    try {
      setCalculators(getCalculators());
      if (props.product) {
        newInputData = inputData;
        const d = props.product.data[props.product.title===selectedProduct?selectedIndex:0]?.inputs.forEach((items) => {
          newInputData[items.shortName] = items.defaultValue;
        });
      }
    } finally {
      setInputData(newInputData);
      setIsMultiple(props.product.data.length>1);
    }
  }, []);

  useEffect(() => {
    let newInputData = null;
    setSelectedIndex(0);
    setIsMultiple(false);
    try {
      if (props.product) {
        newInputData = {};
        const d = props.product.data[props.product.title===selectedProduct?selectedIndex:0]?.inputs.forEach((items) => {
          newInputData[items.shortName] = items.defaultValue;
        });
      }
    } finally {
      setInputData(newInputData);
      setIsMultiple(props.product.data.length>1);
    }
  }, [props]);

  const getValue = (shortName) => {
    if (inputData[shortName]) return inputData[shortName];

    return 0;
  };

  const setValue = (value, shortName) => {
    const newData = { ...inputData };
    newData[shortName] = value;
    setInputData(newData);
  };

  const createChartData =(product)=>{
    const chartInputs = product.chartKeys.map((details)=>{
      if(details.type === "input"){
        return parseInt(inputData[details.parameter]);
      }else {
        return parseInt (details.parameter (inputData));
      }
    });

    const data = Object.create({});
    data.labels = {
      labels: ['Invested Amount', 'Est. Returns'],
      datasets: [
        {
          label: '# of Votes',
          backgroundColor: [
            'rgba(59, 127, 188, 1)',
            'rgba(240, 64, 69, 1)',
          ],
          borderColor: [
            'rgba(59, 127, 188, 1)',
            'rgba(240, 64, 69, 1)',
          ],
          borderWidth: 1,
        },
      ],
    };
    data.labels = ['Invested Amount', 'Est. Returns'];
    data.datasets = [];
    data.datasets.push(Object.create({}));
    data.datasets[0].label ='startofbots';
    data.datasets[0].data = chartInputs;
    data.datasets[0].backgroundColor = [
      'rgba(59, 127, 188, 1)',
      'rgba(240, 64, 69, 1)',
    ];
    data.datasets[0].borderColor = [
      'rgba(59, 127, 188, 1)',
      'rgba(240, 64, 69, 1)',
    ];
    data.datasets[0].borderWidth = 1;
    return data;
  }


  return (
    <div className="App">
      <div className="container container-wrapper">
        <div className="row">
          <div className="col-lg-8 p-4 bg-calc">
          <h2 className="text-center mb-4">{props.product.title}</h2>
            <div className="col-lg-7 col-md-6 mb-4">
              {isMultiple &&
                props.product.data.map((element, index) => {
                  return (
                    <label className="containerradio">
                      {element.title}
                      <input
                        type="radio"
                        onChange={(ev) => setSelectedIndex(index)}
                        checked={selectedIndex === index}
                        name="radio"
                      />
                      <span className="checkmark"></span>
                    </label>
                  );
                })}

              {props.product.data[props.product.title===selectedProduct?selectedIndex:0] &&
                props.product.data[props.product.title===selectedProduct?selectedIndex:0].inputs.map((item) => {
                  return (
                    <>
                      <h5 className="mt-4">{item.title}</h5> <h5>{getValue(item.shortName)}</h5>
                      <input
                        type="range"
                        min={item.min}
                        max={item.max}
                        step={item.step}
                        value={getValue(item.shortName)}
                        onChange={(event) => setValue(event.target.value, item.shortName)}
                        className="slider"
                        id="myRange"
                      />
                    </>
                  );
                })}
            </div>

            <div className="col-lg-5 col-md-6">
              <ChartApp data={createChartData(props.product.data[props.product.title===selectedProduct?selectedIndex:0])}/>
              <div className="text-center mt-4"><a href="" className="btn btn-primary">CTA</a></div>
            </div>
            <div className="col-lg-12 mb-4">
              {props.product.data[props.product.title===selectedProduct?selectedIndex:0] &&
                props.product.data[props.product.title===selectedProduct?selectedIndex:0].outputs.map((item) => {
                  return (
                    <h5 className="mb-4">
                      {item.title}
                      <p>{getOutput(item.formula, inputData)}</p>
                    </h5>
                  );
                })}
            </div>
          </div>

          <div className="col-lg-4">
            <div className="category-list calCulate">
              <h3>Calculators</h3>
              {calculators.length>0 && calculators.map(calculator =>
                  <div className="calculatorType" onClick={() => props.handleSelection(calculator.keyName)}>{calculator.title}</div>)
                }
            </div>
          </div>

        </div>
      </div>
    </div>
  );
}

export default function CalcApp(props) {
  const [productName, setProductName] = useState("mSIP");

  const handleSelection = (productName) => {
    setProductName(productName);
    props.onCalcChange(productName);
  }

  return <Calc product={getCalculatorData(productName)} handleSelection={handleSelection} />;
}