import React from 'react';
import { Container, Nav, Navbar, NavDropdown } from 'react-bootstrap';
import './Navbar.css';
import logo from '../../Layouts/Home/images/LOGO.webp';
import { withTranslation } from 'react-i18next';
import i18n from "../../i18n/config";
import history from '../../history';



function Navmenu(props) {
  const handleSelection = (selectedProduct) => {
    history.push(`/${selectedProduct}`)
 }
  return (
    //<div className="container">
      //<div className="Navmenu">
      <Container>
        <Navbar variant="light" sticky="top" expand="md" collapseOnSelect>

          <Navbar.Brand>
            <a href="/">
              <img src={logo} width="240vw" height="auto" className='logo-img' /></a>{' '}
          </Navbar.Brand>
          <Navbar.Toggle className="coloring" />
          <Navbar.Collapse className="right-aligned">
            <Nav>
              <NavDropdown title="Products">
                <NavDropdown.Item onClick={()=>handleSelection("MutualFund")}>Mutual Fund</NavDropdown.Item>
                <NavDropdown.Item onClick={()=>handleSelection("fixed-deposit")}>Fixed Deposit</NavDropdown.Item>
                <NavDropdown.Item onClick={()=>handleSelection("pms")}>PMS</NavDropdown.Item>
                <NavDropdown.Item onClick={()=>handleSelection("peer-to-peer-lending")}>P2P Loan</NavDropdown.Item>
                <NavDropdown.Item onClick={()=>handleSelection("alternative-investment-fund")}>AIF</NavDropdown.Item>
                <NavDropdown.Item onClick={()=>handleSelection("national-pension-scheme")}>NPS</NavDropdown.Item>
                <NavDropdown.Item onClick={()=>handleSelection("bond")}>Bond</NavDropdown.Item>
                <NavDropdown.Item onClick={()=>handleSelection("gold")}>Gold</NavDropdown.Item>
                <NavDropdown title="Loans" className='loans-menu'>
                <NavDropdown.Item onClick={()=>handleSelection("loans/instant-home-loan")}>Home Loan</NavDropdown.Item>
                <NavDropdown.Item onClick={()=>handleSelection("loans/loan-against-securities")}>Loan Against Securities</NavDropdown.Item>
                </NavDropdown>
              </NavDropdown>
              <NavDropdown title="Partner">
                <NavDropdown.Item href="/partner/become-mutual-fund-distributors">Become MF Distributor</NavDropdown.Item>
                <NavDropdown.Item href="/partner/grow-your-mutual-fund">Grow MF Business</NavDropdown.Item>
                <NavDropdown.Item href="/partner/all-about-amfi-arn-code">AMFI ARN Registration</NavDropdown.Item>
              </NavDropdown>
              <a href="https://app.nivesh.com/home/index" className="register-button">Login/Register</a>

              <NavDropdown title={i18n.language==='en' ? 'English':'Hindi'} className='language-selector'>
                <NavDropdown.Item onClick={() => props.changeLanguage('en')} >English
                </NavDropdown.Item>
                <NavDropdown.Item onClick={() => props.changeLanguage('hn')} >Hindi</NavDropdown.Item>
              </NavDropdown>


            </Nav>
          </Navbar.Collapse>

        </Navbar>
        </Container>
     // </div>
   // </div>


  );
}

export default withTranslation()(Navmenu)